import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";
import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import React from "react";
import Dropzone, { DropzoneProps } from "react-dropzone";

export const styles = (theme: Theme) =>
  createStyles({
    formLabel: {
      marginBottom: theme.spacing.unit
    },
    info: {
      margin: theme.spacing.unit
    },
    root: {
      margin: theme.spacing.unit
    }
  });
export interface IStyledDropzoneProps
  extends WithStyles<typeof styles>,
    DropzoneProps {
  errors?: {};
  storedValue?: File[] | File;
  label: string;
  name: string;
  fullWidth?: boolean;
  required?: boolean;
  setFieldValue: any;
}

const onDrop = (
  setFieldValue: any,
  multiple: boolean,
  name: string,
  storedValue?: File[] | File
) => (acceptedFiles: File[]) => {
  // do nothing if no files
  if (acceptedFiles.length === 0) {
    return;
  }

  if (multiple) {
    if (storedValue) {
      // on drop we add to the existing files
      storedValue = storedValue as File[];
      setFieldValue(name, storedValue.concat(acceptedFiles));
    } else {
      // or we replace the existing files
      setFieldValue(name, acceptedFiles);
    }
  } else {
    // or we replace the existing file
    setFieldValue(name, acceptedFiles[0]);
  }
};
const StyledDropzone: React.SFC<IStyledDropzoneProps> = ({
  classes,
  errors,
  storedValue,
  label,
  fullWidth = false,
  multiple = true,
  required = true,
  setFieldValue,
  ...props
}) => {
  let content: JSX.Element;
  if (storedValue) {
    if (multiple) {
      storedValue = storedValue as File[];
      content = (
        <div>
          {storedValue.map((file: File) => (
            <Typography
              align="center"
              noWrap={true}
              className={classes.info}
              key={file.name}
            >
              {file.name}
            </Typography>
          ))}
        </div>
      );
    } else {
      storedValue = storedValue as File;
      content = (
        <Typography align="center" noWrap={true} className={classes.info}>
          {storedValue.name}
        </Typography>
      );
    }
  } else {
    content = (
      <Typography align="center" className={classes.info}>
        Click or drop {multiple ? "files" : "file"} here
      </Typography>
    );
  }
  return (
    <FormControl
      component="fieldset"
      fullWidth={fullWidth}
      required={required}
      error={!!errors}
      className={classes.root}
    >
      <FormLabel component="legend" className={classes.formLabel}>
        {label}
      </FormLabel>
      <Dropzone
        onDrop={onDrop(setFieldValue, multiple, props.name, storedValue)}
        multiple={multiple}
        {...props}
      >
        {({
          isDragActive,
          isDragReject
        }: {
          isDragActive: boolean;
          isDragReject: boolean;
          acceptedFiles: any[];
          rejectedFiles: any[];
        }) => {
          if (isDragActive) {
            return (
              <Typography align="center" className={classes.info}>
                This file type is permitted.
              </Typography>
            );
          }
          if (isDragReject) {
            return (
              <Typography align="center" className={classes.info}>
                This file type is not permitted.
              </Typography>
            );
          }
          return <div>{content}</div>;
        }}
      </Dropzone>
      {errors && <FormHelperText>{errors}</FormHelperText>}
    </FormControl>
  );
};
export default withStyles(styles)(StyledDropzone);



