import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { FormikProps } from "formik";
import React from "react";
import { IDftFilesFMO } from "../types";
import StyledDropzone from "./StyledDropzone";
import SubmitButton from "./SubmitButton";

const styles = (theme: Theme) =>
  createStyles({
    formControl: {
      margin: theme.spacing.unit
    }
  });
export interface IDftFilesFMOProps
  extends FormikProps<IDftFilesFMO>,
    WithStyles<typeof styles> {}
const DftFilesFMO: React.SFC<IDftFilesFMOProps> = ({ classes, ...props }) => (
  <form onSubmit={props.handleSubmit}>
    <StyledDropzone
      fullWidth={true}
      disablePreview={true}
      multiple={false}
      name="file"
      errors={props.errors.file}
      label="File"
      storedValue={props.values.file}
      setFieldValue={props.setFieldValue}
    />
    <SubmitButton
      error={Object.keys(props.errors).length !== 0}
      isSubmitting={props.isSubmitting}
      buttonText="Submit"
      ButtonIcon={CloudUploadIcon}
    />
  </form>
);
export default withStyles(styles)(DftFilesFMO);



